@use 'variables' as v;

@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin ipad {
    @media (min-width: v.$ipad) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

@mixin grid($columnas, $espaciadocolumn: 0rem, $espaciadorow: 0rem) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    column-gap: $espaciadocolumn;
    row-gap: $espaciadorow;
}

@mixin resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}
