@use 'base/variables' as v;
@use 'base/mixins' as m;

.setlist {
    background-color: v.$brown;
    padding: 3rem 0;


    h3, p {
        text-align: center;
        color: v.$black;
    }

    .fecha, .encore {
        font-weight: bold;
    }

    .fecha {
        margin-bottom: 5rem;
        // margin-top: 0;

        &:after {
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;
            
        }
        
    }

    .viernes .fecha:after {
        background-image: url(../img/icono_rock.png);
    }

    .sabado .fecha:after {
        background-image: url(../img/icono_edm.png);
    }    

    

}

.contenedor-setlist {
    .lista {
        @include m.resetear-lista;
        li {
            padding: 2rem 2rem;
            font-weight: bold;
        }
        // letter-spacing: 1px;
    }

    
    	
    @include m.ipad {
        @include m.grid(2, 4rem);
        width: 90%;
    }

    .bg-cherry {
        background-color: v.$cherry;

        li:nth-child(odd) {
            background-color: darken(v.$cherry, 5%);
        }
    }

    .bg-gray {
        background-color: v.$gray;

        li:nth-child(odd) {
            background-color: darken(v.$gray, 10%);
        }
    }

    .setlist-dia {
        padding: 3rem 0rem;
        margin-bottom: 5rem;   
    }



}

