@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;
    
    .overlay {
        position: absolute;
        background: rgb(85,25,77);
        background: linear-gradient(270deg, rgba(85,25,77,0.8723897911832946) 0%, rgba(121,58,87,0.7447795823665893) 33%, rgba(148,99,120,0.765661252900232) 59%, rgba(143,141,146,0.7238979118329467) 81%, rgba(184,165,151,0.5568445475638051) 100%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

    }

    video {
        width: auto;
        display: block;
        max-height: 50rem;

        @include m.desktop {
            max-height: unset;
            width: 100%;
        }
    }

    .contenido-video {
        z-index: 10;
        h2 {
            color: v.$white;
            font-size: 7rem;
            display: flex;
            flex-direction: column;
            margin: 0;
            overflow: auto;
        }

        p {
            background-color: v.$cherry;
            color: v.$white;
            font-weight: bold;
            font-size: 2.4rem;
            display: inline-block;
            padding: 1rem 2rem;
            border-radius: 2.5px;
        }
    }
}