@use 'base/variables' as v;
@use 'base/mixins' as m;

.boletos {
    padding: 5rem 0rem;
    background-color: v.$cherry;
    

    h3 {
        text-align: center;
        color: v.$white;        
    }

    p {
        margin: 0
    }


    .contenedor-boletos {
        @include m.ipad {
            @include m.grid(2, 2rem, 2rem);
        }

        @include m.desktop {
            @include m.grid(4, 2rem, 2rem)
        }
        
        
    }

    picture {
        display: grid;
        place-items: center;
        padding: 2rem 0;
        img {
            width: 90%;
            max-width: 100rem;

        }
    }
}

.boleto {
    margin: 1rem;
    
    &-header {
        font-weight: normal;
    }

    &-footer {
        background-color: v.$gray;
        font-weight: bold;
        font-size: 2.4rem;
        
    }

    .general-a {
        background-color: #F82D33;
    }

    .general-b {
        background-color: #1E96B9;
    }

    .verde-a {
        background-color: #05693C;
    }

    .naranja-a {
        background-color: #F55929;
    }

    .verde-b {
        background-color: #37B54B;
    }

    .naranja-b {
        background-color: #FA931E;
    }

    .verde-c {
        background-color: #8DC63D;
    }

    .naranja-c {
        background-color: #FEAF3A;
    }

}