html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Monserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
@media (min-width: 768px) {
  body.body-scroll {
    padding-top: 13.2rem;
  }
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2.8rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #b8a597;
  padding: 1.5rem 0;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.1rem 0.9rem #333;
  }
}
.header h1 {
  color: #000000;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 480px) {
  .header h1 {
    display: flex;
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
}

.navegacion-principal {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.navegacion-principal a {
  color: #000000;
  font-size: 2.4rem;
}
.navegacion-principal a:hover {
  color: #793a57;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(85, 25, 77);
  background: linear-gradient(270deg, rgba(85, 25, 77, 0.8723897912) 0%, rgba(121, 58, 87, 0.7447795824) 33%, rgba(148, 99, 120, 0.7656612529) 59%, rgba(143, 141, 146, 0.7238979118) 81%, rgba(184, 165, 151, 0.5568445476) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
  max-height: 50rem;
}
@media (min-width: 992px) {
  .video video {
    max-height: unset;
    width: 100%;
  }
}
.video .contenido-video {
  z-index: 10;
}
.video .contenido-video h2 {
  color: #FFFFFF;
  font-size: 7rem;
  display: flex;
  flex-direction: column;
  margin: 0;
  overflow: auto;
}
.video .contenido-video p {
  background-color: #793a57;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 2.4rem;
  display: inline-block;
  padding: 1rem 2rem;
  border-radius: 2.5px;
}

.sobre-conciertos {
  padding: 5rem 0;
}
@media (min-width: 992px) {
  .sobre-conciertos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 4rem;
    row-gap: 0rem;
  }
}

.contenido-conciertos {
  margin-top: 2rem;
}
@media (min-width: 992px) {
  .contenido-conciertos {
    margin-top: 0;
  }
}
.contenido-conciertos h2 {
  margin-bottom: 3rem;
}
.contenido-conciertos .highlight {
  color: #793a57;
  font-weight: bold;
}

@media (min-width: 992px) {
  .mas-contenido {
    grid-column: 1/3;
    grid-row: 2/3;
  }
}

.setlist {
  background-color: #b8a597;
  padding: 3rem 0;
}
.setlist h3, .setlist p {
  text-align: center;
  color: #000000;
}
.setlist .fecha, .setlist .encore {
  font-weight: bold;
}
.setlist .fecha {
  margin-bottom: 5rem;
}
.setlist .fecha:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}
.setlist .viernes .fecha:after {
  background-image: url(../img/icono_rock.png);
}
.setlist .sabado .fecha:after {
  background-image: url(../img/icono_edm.png);
}

.contenedor-setlist .lista {
  margin: 0;
  padding: 0;
  list-style: none;
}
.contenedor-setlist .lista li {
  padding: 2rem 2rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  .contenedor-setlist {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 4rem;
    row-gap: 0rem;
    width: 90%;
  }
}
.contenedor-setlist .bg-cherry {
  background-color: #793a57;
}
.contenedor-setlist .bg-cherry li:nth-child(odd) {
  background-color: #68324b;
}
.contenedor-setlist .bg-gray {
  background-color: #8f8d92;
}
.contenedor-setlist .bg-gray li:nth-child(odd) {
  background-color: #757379;
}
.contenedor-setlist .setlist-dia {
  padding: 3rem 0rem;
  margin-bottom: 5rem;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem;
  row-gap: 2rem;
}
.galeria-imagenes picture {
  width: auto;
}
.galeria-imagenes img {
  width: 300px;
  height: 120px;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
    row-gap: 2rem;
  }
  .galeria-imagenes img {
    height: 150px;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 2rem;
    row-gap: 2rem;
  }
  .galeria-imagenes img {
    height: 120px;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  justify-content: center;
  max-width: 100rem;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: bold;
}

.boletos {
  padding: 5rem 0rem;
  background-color: #793a57;
}
.boletos h3 {
  text-align: center;
  color: #FFFFFF;
}
.boletos p {
  margin: 0;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    row-gap: 2rem;
  }
}
@media (min-width: 992px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 2rem;
    row-gap: 2rem;
  }
}
.boletos picture {
  display: grid;
  place-items: center;
  padding: 2rem 0;
}
.boletos picture img {
  width: 90%;
  max-width: 100rem;
}

.boleto {
  margin: 1rem;
}
.boleto-header {
  font-weight: normal;
}
.boleto-footer {
  background-color: #8f8d92;
  font-weight: bold;
  font-size: 2.4rem;
}
.boleto .general-a {
  background-color: #F82D33;
}
.boleto .general-b {
  background-color: #1E96B9;
}
.boleto .verde-a {
  background-color: #05693C;
}
.boleto .naranja-a {
  background-color: #F55929;
}
.boleto .verde-b {
  background-color: #37B54B;
}
.boleto .naranja-b {
  background-color: #FA931E;
}
.boleto .verde-c {
  background-color: #8DC63D;
}
.boleto .naranja-c {
  background-color: #FEAF3A;
}

.site-footer {
  background-color: #b8a597;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  font-weight: bold;
  font-size: 1.6rem;
}