@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-conciertos {
    
    // background-color: v.$white;
    padding: 5rem 0;
    

    @include m.desktop {
        @include m.grid(2, 4rem);
    }
}


.contenido-conciertos {
    margin-top: 2rem;

    @include m.desktop {
        margin-top: 0;
        
    }
    h2 {
        margin-bottom: 3rem;
    }

    .highlight {
        color: v.$cherry;
        font-weight: bold;
    }
}

.mas-contenido {
    @include m.desktop {
        grid-column: 1 / 3;
        grid-row: 2 / 3;
    }

}
