@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {
    padding: 5rem 0;

    h3 {
        text-align: center;
    }

}

.galeria-imagenes {
    picture {
        width: auto;
    }

    img {
        width: 300px;
        height: 120px;
    }

    @include m.resetear-lista;
    @include m.grid(2, 2rem, 2rem);
    
    @include m.ipad {
        @include m.grid(3, 2rem, 2rem);
        img {
            height: 150px;
        }
    }

    @include m.desktop {
        @include m.grid(6, 2rem, 2rem);
        img {
            height: 120px;
        }
    }

    
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    // background-color: rgba(0, 0, 0, .5);
    background-color: rgb(0 0 0 / .5);

    picture {
        display: block;
        justify-content: center;
        max-width: 100rem;
    }
}

.btn-cerrar {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$white;
    color: v.$black;

    display: grid;
    place-items: center;

    font-weight: bold;
    
}