@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-color: v.$brown;
    padding: 1.5rem 0;

    @include m.ipad {
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .3rem .1rem .9rem #333;
        }
    }
    

    h1 {
        color: v.$black;
        text-align: center;
        margin-bottom: 1rem;

        @include m.phone {
            display: flex;
            flex-direction: column;
        }
    }

    .contenido-header {
        @include m.ipad {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 2rem;
        }
    }
}

// No se recomienda hacer tanta anidación, por lo que podemos colocar las clases dentro o fuera según nos convenga

.navegacion-principal { 
    display: flex;
    justify-content: center;
    gap: 2rem;

    a {
        color: v.$black;
        font-size: 2.4rem;
    
        &:hover {
            color: v.$cherry;
        }
    }
}


